.name__navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 4rem;
    position: sticky;
    top: 0;
    right: 0;
    left: 0;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.85);
    -webkit-backdrop-filter: blur(12px);
    backdrop-filter: blur(12px);
}

.name__navbar-links{
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.name__navbar-links_logo{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    margin-right: 1rem;
}

.name__navbar-links_logo img{
    width: 62px;
    height: 62px;
}

.name__navbar-links_container{
    display: flex;
    flex-direction: row;
}

.name__navbar-links_container p{
    color: #121212;
    font-family: var(--font-family);
    font-weight: 500;
    font-style: normal;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;

    cursor: pointer;
}
.name__navbar-menu{
    margin-left: 1rem;
    display: none;
    position: relative;

}

.name__navbar-menu svg{
    cursor: pointer;
}

.name__navbar-menu_container{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    text-align: end;
    background-color: #ffffff00;
    padding: 1rem;
    position: absolute;
    top: 70px;
    right: 0;
    margin: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0,0,0,0.2);
}


.name__navbar-menu_container p{
    color: #121212;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 30px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
}


@media screen and (max-width: 10000px) {
    .name__navbar-links-container{
        display: none;
    }
    .name__toggle-menu{
        display: none;
    }
}

@media screen and (max-width: 700px) {
    .name__navbar{
        padding: 2rem 4rem;
    }
    .name__toggle-menu{
        display: none;
    }
}

@media screen and (max-width: 575px) {
    .name__navbar{
        padding: 2rem;
    }

    .name__navbar-menu_container{
        top:57px;
    }

    .name__navbar-links{
        display: none;
    }
    .name__toggle-menu{
        display: flex;
    }
}

